import React from "react";
import Layout from "../components/layout";
import Tabs from "../components/tabs";
import Member from "../components/member";
import Button from "../components/button";
import { graphql } from "gatsby"
import DB from "../components/db";

const AddMember = props => (
  <div className="level" style={{marginBottom: `10px`}}>
    <div className="level-item">
      <Button onClick= {props.onClick} class="fas fa-lg fa-user-alt has-text-info" text="Kara Mamba" />
    </div>
  </div>
)

class MembersList extends React.Component {

  constructor(props) {
    super(props);
    this.addMember = this.addMember.bind(this);
    this.state = {
      members: []
    };
    this.numbers = ["754238536", "05489806", "654593536", "15463536", "95464536"]
    this.names = ["Assibi", "Cof", "Coffi", "Coffie", "Colee", "Coley", "Colie", "Dura", "Durah", "Durrah", "Fara", "Fari", "Farih", "Farra", "Farry", "Gaddo", "Gambo", "Jummai", "Ladi"];
    this.pics = props.pics;
  }

  componentDidMount() {
    DB.table('members')
      .toArray()
      .then((members) => {
        this.setState({ members });
      });
  }

  addMember() {
    console.log("button pressed");
    const member = {
      imgIndex: Math.floor(Math.random() * (this.pics.length - 1)),
      name: this.names[Math.floor(Math.random() * (this.names.length - 1))],
      phone: this.numbers[Math.floor(Math.random() * (this.numbers.length - 1))],
      shares: Math.floor(Math.random() * 50) + 1,
      loans: Math.floor(Math.random() * 20) + 1
    }
    DB.table('members')
      .add(member)
      .then((id) => {
        const newMember = [member, ...this.state.members];
        this.setState({ members: newMember });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    return (
      <div>
        <AddMember onClick={this.addMember}/>
        {this.state.members.map((member) => (
          <Member key={member.id} img={this.pics[member.imgIndex]} name={member.name} phone={member.phone} shares={member.shares} loans={member.loans} />
        ))}
      </div>
   )
  }
}

export default ({data}) => {
  return(
    <Layout>
      <Tabs />
      <MembersList pics={data.pics.edges}/>
    </Layout>
  );
}
export const query = graphql`
  query {
    pics: allFile(filter: {relativeDirectory: {eq: "members"}}) {
    edges {
      node {
        childImageSharp {
          # Specify the image processing specifications right in the query.
          # Makes it trivial to update as your page's design changes.
          fixed(width: 64, height: 64) {
            ...GatsbyImageSharpFixed
          }
        }
      }
   }
  }
}
`
